import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

// –û—Ç—Ä–∏–º—É—î–º–æ —Ñ–æ—Ä–º—É –ø—ñ–¥–ø–∏—Å–∫–∏ —Ç–∞ –ø–æ–ª–µ –¥–ª—è –≤–≤–æ–¥—É e-mail
const formElement = document.querySelector('.sub-form');
const emailInput = document.querySelector('#user-email');

// –î–æ–¥–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó –Ω–∞ –≤—ñ–¥–ø—Ä–∞–≤–∫—É —Ñ–æ—Ä–º–∏
formElement.addEventListener('submit', processFormSubmission);

function processFormSubmission(e) {
  e.preventDefault();

  const enteredEmail = emailInput.value.trim();
  const isEmailValid = validateEmailFormat(enteredEmail);

  // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –∫–æ—Ä–µ–∫—Ç–Ω–æ—Å—Ç—ñ e-mail
  if (!isEmailValid) {
    showToast('error', 'Invalid Input', 'Please provide a valid email address');
    return;
  }

  const existingEmails = getStoredEmails();

  // –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –Ω–∞ —Ç–µ —á–∏ –≤–∂–µ –ø—ñ–¥–ø–∏—Å–∞–Ω–∏–π –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á
  if (isEmailRegistered(existingEmails, enteredEmail)) {
    showToast('warning', 'Already Subscribed', 'This email is already on the list');
    resetForm();
    return;
  }

  // –î–æ–¥–∞—î–º–æ –Ω–æ–≤—É –∞–¥—Ä–µ—Å—É –¥–æ —Å–ø–∏—Å–∫—É —Ç–∞ –∑–±–µ—Ä—ñ–≥–∞—î–º–æ
  storeEmail(existingEmails, enteredEmail);
  showToast('success', 'Subscribed', 'You have successfully joined the list');
  resetForm();
}

// –í–∞–ª—ñ–¥–∞—Ü—ñ—è e-mail –∑–∞ –¥–æ–ø–æ–º–æ–≥–æ—é —Ä–µ–≥—É–ª—è—Ä–Ω–æ–≥–æ –≤–∏—Ä–∞–∑—É
function validateEmailFormat(email) {
  const pattern = /^\w+(\.\w+)?@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/;
  return pattern.test(email);
}

// –û—Ç—Ä–∏–º—É—î–º–æ –∑–±–µ—Ä–µ–∂–µ–Ω—ñ e-mail –∑ localStorage
function getStoredEmails() {
  const data = localStorage.getItem('subscriptionList');
  return data ? JSON.parse(data) : [];
}

// –ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –Ω–∞ —Ç–µ —á–∏ e-mail –≤–∂–µ —î —É —Å–ø–∏—Å–∫—É
function isEmailRegistered(list, email) {
  return list.includes(email);
}

// –î–æ–¥–∞—î–º–æ –Ω–æ–≤–∏–π e-mail –¥–æ —Å–ø–∏—Å–∫—É —Ç–∞ –æ–Ω–æ–≤–ª—é—î–º–æ localStorage
function storeEmail(list, email) {
  list.push(email);
  localStorage.setItem('subscriptionList', JSON.stringify(list));
}

// –í—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è —Å–ø–æ–≤—ñ—â–µ–Ω–Ω—è –∑–∞ –¥–æ–ø–æ–º–æ–≥–æ—é iziToast
function showToast(type, title, msg) {
  iziToast[type]({
    title: title,
    message: msg,
    position: 'topRight',
  });
}

// –°–∫–∏–¥–∞—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è —Ñ–æ—Ä–º–∏ –ø—ñ—Å–ª—è –æ–±—Ä–æ–±–∫–∏
function resetForm() {
  formElement.reset();
}