const slides = document.querySelectorAll('.events-item');
const indicators = document.querySelectorAll('.events-indicator');
const prevBtn = document.getElementById('prevBtn');
const nextBtn = document.getElementById('nextBtn');

// –ó–º—ñ–Ω–Ω—ñ –¥–ª—è –≤—ñ–¥—Å—Ç–µ–∂–µ–Ω–Ω—è –ø–æ—Ç–æ—á–Ω–æ–≥–æ —Å—Ç–∞–Ω—É
let currentSlide = 0;       // –î–ª—è –º–æ–±—ñ–ª—å–Ω–∏—Ö
let currentPage = 0;        // –î–ª—è –ø–ª–∞–Ω—à–µ—Ç—ñ–≤
const totalSlides = slides.length;

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è
function updateDisplay() {
    // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ —Ä–æ–∑–º—ñ—Ä –µ–∫—Ä–∞–Ω—É
    const isTablet = window.innerWidth >= 768;
    
    if (isTablet) {
        // –õ–æ–≥—ñ–∫–∞ –¥–ª—è –ø–ª–∞–Ω—à–µ—Ç—ñ–≤
        hideAllSlides();
        
        if (currentPage === 0) {
            // –ü–æ–∫–∞–∑—É—î–º–æ –ø–µ—Ä—à—ñ 2 –ø–æ–¥—ñ—ó
            slides[0].style.display = 'block';
            if (slides[1]) slides[1].style.display = 'block';
        } else {
            // –ü–æ–∫–∞–∑—É—î–º–æ —Ç—Ä–µ—Ç—é –ø–æ–¥—ñ—é
            if (slides[2]) slides[2].style.display = 'block';
        }
        
        updateIndicators(currentPage);
        updateButtons(currentPage, 1); // –ú–∞–∫—Å–∏–º—É–º 2 —Å—Ç–æ—Ä—ñ–Ω–∫–∏
    } else {
        // –õ–æ–≥—ñ–∫–∞ –¥–ª—è –º–æ–±—ñ–ª—å–Ω–∏—Ö
        hideAllSlides();
        slides[currentSlide].style.display = 'block';
        
        updateIndicators(currentSlide);
        updateButtons(currentSlide, totalSlides - 1);
    }
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø—Ä–∏—Ö–æ–≤—É–≤–∞–Ω–Ω—è –≤—Å—ñ—Ö —Å–ª–∞–π–¥—ñ–≤
function hideAllSlides() {
    slides.forEach(slide => {
        slide.style.display = 'none';
    });
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä—ñ–≤
function updateIndicators(activeIndex) {
    indicators.forEach((indicator, index) => {
        if (index === activeIndex) {
            indicator.classList.add('active');
        } else {
            indicator.classList.remove('active');
        }
    });
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –æ–Ω–æ–≤–ª–µ–Ω–Ω—è –∫–Ω–æ–ø–æ–∫
function updateButtons(current, max) {
    prevBtn.disabled = (current === 0);
    nextBtn.disabled = (current === max);
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥—É –Ω–∞–∑–∞–¥
function goToPrevious() {
    const isTablet = window.innerWidth >= 768;
    
    if (isTablet) {
        if (currentPage > 0) {
            currentPage = currentPage - 1;
            updateDisplay();
        }
    } else {
        if (currentSlide > 0) {
            currentSlide = currentSlide - 1;
            updateDisplay();
        }
    }
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥—É –≤–ø–µ—Ä–µ–¥
function goToNext() {
    const isTablet = window.innerWidth >= 768;
    
    if (isTablet) {
        if (currentPage < 1) {
            currentPage = currentPage + 1;
            updateDisplay();
        }
    } else {
        if (currentSlide < totalSlides - 1) {
            currentSlide = currentSlide + 1;
            updateDisplay();
        }
    }
}

// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ—Ö–æ–¥—É –¥–æ –∫–æ–Ω–∫—Ä–µ—Ç–Ω–æ–≥–æ —Å–ª–∞–π–¥—É/—Å—Ç–æ—Ä—ñ–Ω–∫–∏
function goToSpecific(index) {
    const isTablet = window.innerWidth >= 768;
    
    if (isTablet) {
        currentPage = index;
    } else {
        currentSlide = index;
    }
    updateDisplay();
}

// –ü—ñ–¥–∫–ª—é—á–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫–∏ –ø–æ–¥—ñ–π
prevBtn.addEventListener('click', goToPrevious);
nextBtn.addEventListener('click', goToNext);

// –û–±—Ä–æ–±–Ω–∏–∫–∏ –¥–ª—è —ñ–Ω–¥–∏–∫–∞—Ç–æ—Ä—ñ–≤
indicators.forEach((indicator, index) => {
    indicator.addEventListener('click', function() {
        goToSpecific(index);
    });
});

// –ù–∞–≤—ñ–≥–∞—Ü—ñ—è –∫–ª–∞–≤—ñ–∞—Ç—É—Ä–æ—é
document.addEventListener('keydown', function(event) {
    if (event.key === 'ArrowLeft') {
        goToPrevious();
    }
    if (event.key === 'ArrowRight') {
        goToNext();
    }
});

// –û–±—Ä–æ–±–∫–∞ –∑–º—ñ–Ω–∏ —Ä–æ–∑–º—ñ—Ä—É –≤—ñ–∫–Ω–∞
window.addEventListener('resize', updateDisplay);

// –û–±—Ä–æ–±–Ω–∏–∫–∏ –¥–ª—è –∫–Ω–æ–ø–æ–∫ Register
document.querySelectorAll('.events-register').forEach(function(button) {
    button.addEventListener('click', function() {
        const eventName = this.closest('.events-item').querySelector('.events-name').textContent;
        openModal(eventName);
    });
});

// –§—É–Ω–∫—Ü—ñ—ó –¥–ª—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞
function openModal(eventName) {
    const modal = document.getElementById('contactModal');
    const eventNameElement = document.getElementById('modalEventName');
    
    eventNameElement.textContent = eventName;
    modal.classList.add('active');
    document.body.style.overflow = 'hidden';
}

function closeModal() {
    const modal = document.getElementById('contactModal');
    modal.classList.remove('active');
    document.body.style.overflow = '';
    
    // –û—á–∏—â–∞—î–º–æ —Ñ–æ—Ä–º—É
    document.getElementById('modalForm').reset();
}

// –û–±—Ä–æ–±–Ω–∏–∫–∏ –ø–æ–¥—ñ–π –¥–ª—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –≤—ñ–∫–Ω–∞
document.getElementById('closeModal').addEventListener('click', closeModal);

// –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ –∫–ª—ñ–∫—É –Ω–∞ —Ñ–æ–Ω
document.getElementById('contactModal').addEventListener('click', function(event) {
    if (event.target === this) {
        closeModal();
    }
});

// –ó–∞–∫—Ä–∏—Ç—Ç—è –ø–æ Escape
document.addEventListener('keydown', function(event) {
    if (event.key === 'Escape') {
        closeModal();
    }
});

// –û–±—Ä–æ–±–∫–∞ –≤—ñ–¥–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º–∏
document.getElementById('modalForm').addEventListener('submit', function(event) {
    event.preventDefault();
    
    const formData = new FormData(this);
    const name = formData.get('name');
    const email = formData.get('email');
    const message = formData.get('message');
    
    console.log('Form submitted:', { name, email, message });
    alert('–î—è–∫—É—î–º–æ –∑–∞ —Ä–µ—î—Å—Ç—Ä–∞—Ü—ñ—é!');
    
    closeModal();
});

// –ó–∞–ø—É—Å–∫–∞—î–º–æ –ø—Ä–∏ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—ñ —Å—Ç–æ—Ä—ñ–Ω–∫–∏
updateDisplay();