import Accordion from 'accordion-js';
import 'accordion-js/dist/accordion.min.css';
import { getBookDetails } from './api';
import '../css/modal-window.css';

// console.log('Modal script –ø–æ–¥–∫–ª—é—á–∏–ª—Å—è!');

document.addEventListener('DOMContentLoaded', () => {
  const backdrop = document.querySelector('.modal-backdrop');
  const quantityEl = document.getElementById('quantity');
  const closeBtn = document.querySelector('.modal-close');

  let currentQuantity = 1;

  if (!backdrop || !quantityEl || !closeBtn) {
    console.warn('–≠–ª–µ–º–µ–Ω—Ç—ã –º–æ–¥–∞–ª–∫–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω—ã');
    return;
  }

  // –î–µ–ª–µ–≥–∏—Ä–æ–≤–∞–Ω–∏–µ –¥–ª—è –ª—é–±—ã—Ö –∫–Ω–æ–ø–æ–∫ –≤–Ω—É—Ç—Ä–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã —Å data-id
  document.addEventListener('click', e => {
    const btn = e.target.closest('[data-id]');
    if (!btn) return;

    const bookId = btn.dataset.id;
    // console.log('–ö–ª–∏–∫ –ø–æ –∫–Ω–æ–ø–∫–µ —Å id:', bookId);
    if (bookId) openModal(bookId);
  });

  // –ö–ª–∏–∫ –ø–æ –∑–∞—Ç–µ–º–Ω–µ–Ω–∏—é –∏–ª–∏ –∫—Ä–µ—Å—Ç–∏–∫—É ‚Äî –∑–∞–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª–∫—É
  backdrop.addEventListener('click', e => {
    if (
      e.target.classList.contains('modal-backdrop') ||
      e.target.closest('.modal-close')
    ) {
      closeModal();
    }
  });

  // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ Esc
  document.addEventListener('keydown', e => {
    if (e.key === 'Escape') closeModal();
  });

  // –£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ–º
  document.addEventListener('click', e => {
    if (e.target.classList.contains('decrement')) decreaseQuantity();
    if (e.target.classList.contains('increment')) increaseQuantity();
    if (e.target.classList.contains('add-to-cart')) addToCart();
    if (e.target.classList.contains('buy-now')) buyNow(e);
  });

  // —É–±–∏—Ä–∞–µ–º –±–∞–≥ —Å –¥—É–±–ª–∏—Ä–æ–≤–∞–Ω–∏–µ–º –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–æ–≤ –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏
  let accordionInstance = null;

  async function openModal(bookId) {
    const data = await getBookDetails(bookId);
    if (!data) {
      console.warn('–ù–µ—Ç –¥–∞–Ω–Ω—ã—Ö –ø–æ –∫–Ω–∏–≥–µ');
      return;
    }

    document.getElementById('modal-title').textContent = data.title;
    document.getElementById('modal-author').textContent = data.author;
    document.getElementById('modal-price').textContent = `$${data.price}`;
    document.getElementById('modal-book-image').src = data.book_image;
    document.getElementById('modal-book-image').alt = data.title;
    document.getElementById('details-text').textContent =
      data.description || 'No description';
    document.getElementById('shipping-text').textContent =
      'We ship across the United States within 2‚Äì5 business days. All orders are processed through USPS or a reliable courier service. Enjoy free standard shipping on orders over $50.';
    document.getElementById('returns-text').textContent =
      'You can return an item within 14 days of receiving your order, provided it hasn‚Äôt been used and is in its original condition. To start a return, please contact our support team ‚Äî we‚Äôll guide you through the process quickly and hassle-free.';

    quantityEl.textContent = 1;
    currentQuantity = 1;

    backdrop.classList.remove('is-hidden');
    document.body.style.overflow = 'hidden';

    // –ï—Å–ª–∏ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç ‚Äî —É–Ω–∏—á—Ç–æ–∂–∞–µ–º
    if (accordionInstance) {
      accordionInstance.destroy();

      // –ñ—ë—Å—Ç–∫–∏–π —Å–±—Ä–æ—Å –∏–Ω–ª–∞–π–Ω-—Å—Ç–∏–ª–µ–π
      document.querySelectorAll('.ac-panel').forEach(panel => {
        panel.style.height = '';
        panel.style.display = '';
      });
    }

    // –°–æ–∑–¥–∞—ë–º –Ω–æ–≤—ã–π –∞–∫–∫–æ—Ä–¥–µ–æ–Ω –∏ —Å–æ—Ö—Ä–∞–Ω—è–µ–º –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é
    accordionInstance = new Accordion('#accordion', {
      duration: 300,
      showMultiple: true,
      openOnInit: [0],
    });
  }

  function closeModal() {
    backdrop.classList.add('is-hidden');
    document.body.style.overflow = '';
    // –£–Ω–∏—á—Ç–æ–∂–∞–µ–º –∞–∫–∫–æ—Ä–¥–µ–æ–Ω –∏ —á–∏—Å—Ç–∏–º —Å—Ç–∏–ª–∏ –ø—Ä–∏ –∑–∞–∫—Ä—ã—Ç–∏–∏
    if (accordionInstance) {
      accordionInstance.destroy();
      accordionInstance = null;

      document.querySelectorAll('.ac-panel').forEach(panel => {
        panel.style.height = '';
        panel.style.display = '';
      });
    }
  }

  function increaseQuantity() {
    currentQuantity++;
    quantityEl.textContent = currentQuantity;
  }

  function decreaseQuantity() {
    if (currentQuantity > 1) {
      currentQuantity--;
      quantityEl.textContent = currentQuantity;
    }
  }

  function addToCart() {
    console.log(`–î–æ–¥–∞–Ω–æ —É –∫–æ—Ä–∑–∏–Ω—É: ${currentQuantity} —à—Ç.`);
  }

  function buyNow(e) {
    e.preventDefault();
    alert('–î—è–∫—É—î–º–æ –∑–∞ –ø–æ–∫—É–ø–∫—É');
  }
});
